  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.33;
  transition-property: color, background-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

#moreButton:hover, #moreButton:focus, .button-ctg:hover, .button-ctg:focus {
  color: #fff;
  background: #4440f6;
}

.button-container {
  flex-flow: row;
  align-items: flex-start;
